import Button from "../../components/Button";
import InputBox from "../../components/InputBox";
import { isEmailValid, isPasswordValid } from "../../utils/validation";
import { useToast } from "../../contexts/toastContext";
import { useAuth } from "../../contexts/authContext";

const Login = ({ formData, handleChange, clearForm, goToStep }) => {
  const toast = useToast();
  const { login } = useAuth();

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { email_phone, loginPassword } = formData;

      // Client side validations
      if (!isEmailValid(email_phone).success) {
        toast.error(isEmailValid(email_phone).message);
        return;
      } else if (!isPasswordValid(loginPassword).success) {
        toast.error(isPasswordValid(loginPassword).message);
        return;
      }

      const payload = { email: email_phone, password: loginPassword };

      login(payload).then(() => {
        clearForm();
      });
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div className="sm:px-4">
      <h2 className="capitalize text-2xl font-semibold text-center mb-16">
        login
      </h2>

      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="flex flex-col gap-4 max-sm:gap-2">
          <InputBox
            label="email / phone"
            name="email_phone"
            value={formData.email_phone}
            onChange={handleChange}
            validation={isEmailValid}
            containerClassName="flex-col gap-2 max-sm:gap-1"
          />
          <InputBox
            type="password"
            label="password"
            name="loginPassword"
            value={formData.loginPassword}
            onChange={handleChange}
            validation={isPasswordValid}
            containerClassName="flex-col gap-2 max-sm:gap-1"
          />
        </div>

        <div className="FORGET-PASS relative -top-3 flex justify-end">
          <p
            className="text-slate-400 text-xs cursor-pointer hover:underline"
            onClick={() => goToStep(3)}
          >
            Forget Password?
          </p>
        </div>

        <Button className="px-10 py-2 rounded-full font-secondary text-sm mx-auto">
          sign in
        </Button>

        <p className="text-center text-sm">
          Don't have an account?{" "}
          <span
            className="text-primeRed hover:underline cursor-pointer"
            onClick={() => goToStep(1)}
          >
            Sign Up
          </span>
        </p>
      </form>
    </div>
  );
};

export default Login;
