import { sendForgetPasswordOtp } from "../../api/userApis";
import Button from "../../components/Button";
import InputBox from "../../components/InputBox";
import { useToast } from "../../contexts/toastContext";
import { isEmailValid } from "../../utils/validation";

const ForgetPassword = ({ formData, handleChange, clearForm, goToStep }) => {
  const toast = useToast();

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { email } = formData;

      // Client side validations
      if (!isEmailValid(email).success) {
        toast.error(isEmailValid(email).message);
        return;
      }

      const response = await sendForgetPasswordOtp(email);

      if (response?.status) {
        console.log(response.data);
        goToStep(6);
      }
    } catch (error) {
      if (!error?.response) {
        toast.error("no server response");
      } else if (error.response) {
        toast.error(error.response?.data?.message);
      } else {
        toast.error("Server issues!!!");
      }
    }
  };

  return (
    <div className="sm:px-4">
      <h2 className="capitalize text-2xl font-semibold text-center mt-8 mb-24">
        forget password
      </h2>

      <form onSubmit={handleSubmit} className="space-y-8">
        <InputBox
          type="email"
          label="Enter Email Address"
          name="email"
          value={formData.email}
          onChange={handleChange}
          validation={isEmailValid}
          align
          containerClassName="flex-col gap-2 max-sm:gap-1"
        />

        <p className="relative -top-3 text-center text-xs text-primary">
          Back to{" "}
          <span
            className="text-primeRed cursor-pointer hover:underline"
            onClick={() => goToStep(2)}
          >
            sign in
          </span>
        </p>

        <div className="mt-8">
          <Button className="w-full px-10 py-2 rounded-full font-secondary text-sm">
            send
          </Button>
        </div>
      </form>
    </div>
  );
};

export default ForgetPassword;
