import Button from "../../components/Button";
import InputBox from "../../components/InputBox";
import { resetPassword } from "../../api/userApis";
import { isPasswordValid } from "../../utils/validation";
import { useToast } from "../../contexts/toastContext";
import { useAuth } from "../../contexts/authContext";

const ResetPassword = ({ formData, handleChange, clearForm }) => {
  const toast = useToast();
  const { login } = useAuth();

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { newPassword, confirmNewPassword } = formData;

      // Client side validation
      if (!isPasswordValid(newPassword).success) {
        console.log(isPasswordValid(newPassword).message);
        return;
      } else if (newPassword !== confirmNewPassword) {
        console.log("password and confirm password should match");
        return;
      }

      const response = await resetPassword({
        email: formData.email,
        password: newPassword,
      });

      if (response?.status) {
        console.log(response.data);
        toast.success("Password updated successfully");

        login({ email: formData.email, password: newPassword }).then(() => {
          clearForm();
        });
      }
    } catch (error) {
      if (!error?.response) {
        toast.error("no server response");
      } else if (error.response) {
        toast.error(error.response?.data?.message);
      } else {
        toast.error("Server issues!!!");
      }
    }
  };

  return (
    <div className="sm:px-4">
      <h2 className="capitalize text-2xl font-semibold text-center mt-8 mb-24">
        rest password
      </h2>

      <form onSubmit={handleSubmit}>
        <div className="flex flex-col gap-4 max-sm:gap-2">
          <InputBox
            type="password"
            label="enter new password"
            name="newPassword"
            value={formData.newPassword}
            onChange={handleChange}
            validation={isPasswordValid}
            containerClassName="flex-col gap-2 max-sm:gap-1"
          />
          <InputBox
            type="password"
            label="confirm password"
            name="confirmNewPassword"
            value={formData.confirmNewPassword}
            onChange={handleChange}
            validation={isPasswordValid}
            containerClassName="flex-col gap-2 max-sm:gap-1"
          />
        </div>

        <div className="mt-10">
          <Button className="w-full py-2 px-6 rounded-full font-secondary">
            save
          </Button>
        </div>
      </form>
    </div>
  );
};

export default ResetPassword;
