import { FaFacebook } from "react-icons/fa";
import bg from "../../assets/images/Rectangle.png";
import OtpVerification from "./OtpVerification";
import Verification from "./Verification";
import ForgetPassword from "./ForgetPassword";
import ResetPassword from "./ResetPassword";
import Signup from "./Signup";
import Login from "./Login";
import useMultiStepForm from "../../hooks/useMultiStepForm";
import { FcGoogle } from "react-icons/fc";

const Auth = () => {
  const { formData, handleChange, clearForm, currentStep, goToStep } =
    useMultiStepForm({}, null, 6, 2);

  const renderStep = () => {
    switch (currentStep) {
      case 1:
        return (
          <Signup
            formData={formData}
            handleChange={handleChange}
            clearForm={clearForm}
            goToStep={goToStep}
          />
        );
      case 2:
        return (
          <Login
            formData={formData}
            handleChange={handleChange}
            clearForm={clearForm}
            goToStep={goToStep}
          />
        );
      case 3:
        return (
          <ForgetPassword
            formData={formData}
            handleChange={handleChange}
            clearForm={clearForm}
            goToStep={goToStep}
          />
        );
      case 4:
        return (
          <Verification
            formData={formData}
            handleChange={handleChange}
            clearForm={clearForm}
            goToStep={goToStep}
          />
        );
      case 5:
        return (
          <ResetPassword
            formData={formData}
            handleChange={handleChange}
            clearForm={clearForm}
            goToStep={goToStep}
          />
        );
      case 6:
        return (
          <OtpVerification
            formData={formData}
            handleChange={handleChange}
            clearForm={clearForm}
            goToStep={goToStep}
          />
        );
      default:
        return (
          <Login
            formData={formData}
            handleChange={handleChange}
            clearForm={clearForm}
            goToStep={goToStep}
          />
        );
    }
  };

  return (
    <div
      className="container max-w-full py-8 max-sm:p-0 bg-cover bg-center bg-no-repeat"
      style={{ backgroundImage: `url(${bg})` }}
    >
      <div className="relative flex flex-col justify-between sm:size-[520px] mx-auto sm:shadow-primary bg-white sm:border sm:border-primary sm:rounded-3xl p-6 space-y-4">
        {renderStep()}

        {(currentStep === 1 || currentStep === 2) && (
          <div className="EXTRA-BUTTONS sm:w-max mx-auto flex flex-wrap gap-8 max-sm:gap-3">
            <button className="max-sm:w-full flex justify-start items-center gap-2 rounded-lg text-sm p-2 bg-white border border-[#dd4b39]">
              <FcGoogle size={24} className="flex-shrink-0" />
              <p>Continue with Google</p>
            </button>
            <button className="max-sm:w-full flex justify-start items-center gap-2 rounded-lg text-sm p-2 bg-white border border-[#3b5998]">
              <FaFacebook size={24} className="flex-shrink-0 text-[#3b5998]" />
              <p>Continue with Facebook</p>
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default Auth;
